import { TokenEndpointOptions, TokenEndpointResponse } from './global';
import { DEFAULT_AUTH0_CLIENT, DEFAULT_TOKEN_PATH } from './constants';
import { getJSON } from './http';
import { createQueryParams } from './utils';

export async function oauthToken(
  {
    baseUrl,
    timeout,
    audience,
    scope,
    auth0Client,
    useFormData,
    disableAuth0Client,
    tokenPath = DEFAULT_TOKEN_PATH,
    ...options
  }: TokenEndpointOptions,
  worker?: Worker
) {
  console.log('oauthToken, tokenPath: ', tokenPath);
  console.log('oauthToken, options: ', options);
  const body = useFormData
    ? createQueryParams(options)
    : JSON.stringify(options);
  console.log('oauthToken, body: ', body);
  console.log('oauthToken, options: ', options);
  console.log('oauthToken, baseUrl: ', baseUrl);

  return await getJSON<TokenEndpointResponse>(
    `${baseUrl}/${tokenPath}`,
    timeout,
    audience || 'default',
    scope,
    {
      method: 'POST',
      body,
      headers: {
        'Content-Type': useFormData
          ? 'application/x-www-form-urlencoded'
          : 'application/json',
        'Auth0-Client': disableAuth0Client
          ? undefined
          : btoa(JSON.stringify(auth0Client || DEFAULT_AUTH0_CLIENT))
      }
    },
    worker,
    useFormData
  );
}
